<template>
  <div>
    <h1>Change your username</h1>
    <h2>Actually your username is {{ username }}</h2>
    <br>
    <br>
    <input type="text" v-model="form.nome"><br><br>
    <button @click="change">Change name</button>
  </div>
</template>

  <script>
  import { mapState } from 'vuex';

  export default {
    data() {
      return {
        form: {
          nome: '',
        },
      };
    },
    computed: {
      ...mapState(['username']),
    },
    methods: {
      change() {
        this.$store.dispatch('changename', Math.random());
      },
    },
    components: {},
  };
</script>

