import { createStore } from 'vuex'

export default createStore({
    state: {
        username: 'Pecris'
    },
    mutations: {
        setUsername(state, val) {
            state.username = val
        }
    },
    actions: {
        async changeName({ commit }, name) {
            commit('setUsername', name)
            localStorage.username = name
        }
    },
    modules: {}
})