<template>

  <body>

    <h2> Username: ({{ username }})</h2>
    <div class="table">
      <center>
        <table>
          <ul>
            <li v-for="nome of connected" :key="nome"><h1>
              {{ nome }}
            </h1></li>
          </ul>
        </table>
      </center>
      <h1>Add tarefa</h1>
      <input type="text" v-model="form.nome"> <button @click="add" key="Add">Add</button>
    </div>
  </body>
</template>

<script>
  // @ is an alias to /src
  import '../app.css'
  import { mapState } from 'vuex';

  export default {
    data() {
      return {
        connected: JSON.parse(localStorage.getItem('connected')) ?? [],
        form: {
          nome: '',
        },
      };
    },
    computed: {
      ...mapState(['username']),
    },
    components: {},
    methods: {
      add() {
        if (this.form.nome.length == 0) {
          return;
        }
        this.connected.push(this.form.nome);
        this.form.nome = '';
        localStorage.setItem('connected', JSON.stringify(this.connected));
      },
      change() {

      },
    },
  };
</script>
