<template>
  <!-- <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.0.1/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-+0n0xVW2eSR5OomGNYDnhzAbDsOXxcvSN1TPprVMTNDbiYZCxYbOOl7+AMvyTG2x" crossorigin="anonymous"> -->
  <header class="header">
    <h1 class="logo">To do</h1>
  </header>

  <div class="menu">
    <h2> {{ username }}'s tasks </h2>
    <input type="text" v-model="changeName.username">{{ changeName.username }}
    <button class="change" @click="change" key="change">
      Change Name
    </button>
  </div>

  <div class="table">
    <table>
      <ul>
        <li v-for="(nome,id) in connected" :key="nome" ref="item.id">
          <div class="button effects">
            {{ nome }}
            <svg width="90" height="90" @click="del(id)" viewBox="0 0 90 90" xmlns="http://www.w3.org/2000/svg">
<path d="M45 0C20.1 0 0 20.1 0 45C0 69.9 20.1 90 45 90C69.9 90 90 69.9 90 45C90 20.1 69.9 0 45 0ZM66.3 59.9C67 60.6 67 61.6
 66.3 62.3L62.2 66.4C61.5 67.1 60.5 67.1 59.8 66.4L45 51.4L30.1 66.3C29.4 67 28.4 67 27.7 66.3L23.6 62.2C22.9 61.5 22.9 60.5
 23.6 59.8L38.6 45L23.7 30.1C23 29.4 23 28.4 23.7 27.7L27.8 23.6C28.5 22.9 29.5 22.9 30.2 23.6L45 38.6L59.9 23.7C60.6 23 61.6
 23 62.3 23.7L66.4 27.8C67.1 28.5 67.1 29.5 66.4 30.2L51.4 45L66.3 59.9Z" fill="white"/>
</svg>
            <!-- <button class="button_del" @click="del(id)" :key="del"><img src="@/assets/trash.svg" width=40 height=40></button> -->
          </div>
        </li>
      </ul>
    </table>
  </div>

  <input class="box" @keyup.enter="add" type="text" ref="item.id" style="font-family: Tahoma; font-size: 3rem" v-model="form.nome">

  <button class="button_add" @click="add" key="Add">
    Add
  </button>

</template>

    <script>
  import { mapState } from 'vuex';
  //import HelloWorld from '@/components/HelloWorld.vue';
  import '../app.css';

  export default {
    data() {
      var compare = 0;
      return {
        connected: JSON.parse(localStorage.getItem('connected')) ?? [],
        form: {
          nome: '',
        },
        changeName: {
          username: '',
        }
      };
    },
    computed: {
      ...mapState(['username']),
    },
    methods: {
      change() {
        if (this.changeName.username.length == 0) {
          return;
        }
        this.username = this.changeName.username
      },
      add() {
        if (this.form.nome.length == 0) {
          return;
        }
        this.connected.push(this.form.nome);
        this.form.nome = '';
        localStorage.setItem('tasks', JSON.stringify(this.connected));
      },
      del(id) {
         this.connected.splice(id, 1);
      },
    },
  };
</script>
